import { createI18n } from 'vue-i18n'

import zh from '../locales/zh.json'
import en from '../locales/en.json'

export const SUPPORTED_LOCALES = {
  'zh-CN': {
    labelKey: 'language.zh'
  },
  'en-US': {
    labelKey: 'language.en'
  }
}

const fallbackLocale = 'zh-CN'
const storedLocale = localStorage.getItem('app-locale')

export const i18n = createI18n({
  legacy: false,
  locale: storedLocale && SUPPORTED_LOCALES[storedLocale] ? storedLocale : fallbackLocale,
  fallbackLocale,
  messages: {
    'zh-CN': zh,
    'en-US': en
  },
  // ç¦ç”¨è­¦å‘Šï¼Œé¿å…æ§åˆ¶å°å™ªéŸ³
  missingWarn: false,
  fallbackWarn: false,
  // å½“æ¶ˆæ¯ç¼ºå¤±æ—¶è¿”å› key è€Œä¸æ˜¯æŠ›å‡ºé”™è¯¯
  missing: (locale, key) => {
    return key
  }
})

export default i18n

